;\n</script>\n\n<template>\n  <button :class=\"className\" :name=\"name\" @click=\"toggle\">\n    <slot />\n  </button>\n</template>\n\n<style scoped>\n.toggle-button {\n  display: inline-block;\n  box-sizing: border-box;\n  padding: var(--paddingMinimal);\n  color: var(--fg);\n  cursor: pointer;\n  background-color: transparent;\n  border: 1px solid transparent;\n  border-radius: var(--borderRadius);\n}\n\n.smallIcon {\n  font-size: var(--fontSizeSmallIcon);\n  line-height: 0;\n}\n\n.default {\n  padding: var(--padding);\n  font-size: var(--fontSizeMedium);\n  line-height: 1;\n}\n\n.icon {\n  font-size: var(--fontSizeIcon);\n  line-height: 0;\n}\n\n.part {\n  font-size: var(--fontSizePart);\n  line-height: 0;\n}\n\n.toggle-button:hover {\n  border-color: var(--primary);\n}\n\n.toggle-button:active {\n  background-color: var(--primaryBg);\n  border-color: var(--primaryActive);\n}\n\n.toggle-button.selected:not(:hover) {\n  border-color: var(--border);\n}\n</style>\n"],"sourceRoot":""}]);
