`, "",{"version":3,"sources":["webpack://./src/components/inputs/FileSelect.vue"],"names":[],"mappings":";AAuEA;EACE,gCAAgC;EAChC,gCAAgC;EAChC,gBAAgB;EAChB,iBAAiB;AACnB","sourcesContent":["<script lang=\"ts\">\nimport { defineComponent } from \"vue\";\nimport { urlToImg } from \"../../utils/canvas\";\nimport { loadFileAsBlobURL } from \"../../utils/file\";\nimport Button from \"./Button.vue\";\nimport File from \"../icons/File.vue\";\n\nexport default defineComponent({\n  components: {\n    Button,\n    File,\n  },\n  props: {\n    label: { type: String, default: undefined },\n    type: { type: String, required: true },\n    name: { type: String, default: \"\" },\n  },\n  emits: [\"load\"],\n  data() {\n    return {\n      file: null as File | null,\n    };\n  },\n  computed: {\n    accept(): string | null {\n      if (this.type === \"img\") {\n        return \"image/*\";\n      } else if (this.type === \"font\") {\n        return \"font/otf,font/ttf,font/woff\";\n      } else {\n        return null;\n      }\n    },\n  },\n  methods: {\n    onClick(): void {\n      (this.$refs.input as HTMLInputElement).click();\n    },\n    onChange(e: { target: { files: FileList } }): void {\n      if (e.target.files[0]) {\n        this.file = e.target.files[0];\n        if (this.type === \"img\") {\n          loadFileAsBlobURL(this.file).then((blobUrl) => {\n            urlToImg(blobUrl, (img) => {\n              if (img.naturalWidth !== 0 && img.naturalHeight !== 0) {\n                this.$emit(\"load\", img);\n              }\n            });\n          });\n        }\n      }\n    },\n  },\n});\n</script>\n\n<template>\n  <input\n    ref=\"input\"\n    type=\"file\"\n    style=\"display: none\"\n    :accept=\"accept\"\n    @change=\"onChange\"\n  />\n  <Button type=\"dashed\" :name=\"name\" @click=\"onClick\">\n    <slot />\n  </Button>\n  <div v-if=\"file\" class=\"file\"><File /> {{ file.name }}</div>\n</template>\n\n<style scoped>\n.file {\n  margin-top: var(--spacingMedium);\n  font-size: var(--fontSizeMedium);\n  color: var(--fg);\n  user-select: none;\n}\n</style>\n"],"sourceRoot":""}]);
