Mode\" :images=\"resultImageUrls\" />\n        <Preview\n          v-if=\"previewMode\"\n          :images=\"resultImageUrls\"\n          :dark-mode=\"false\"\n        />\n        <Preview\n          v-if=\"previewMode\"\n          :images=\"resultImageUrls\"\n          :dark-mode=\"true\"\n        />\n        <Checkbox v-model=\"previewMode\" name=\"\">\n          {{ \"\" }}\n        </Checkbox>\n      </Space>\n    </Card>\n    <Space class=\"buttons\">\n      <Button\n        v-if=\"showTarget\"\n        name=\"()\"\n        @click=\"$emit('toggleShowTarget', $event)\"\n      >\n        <template #icon>\n          <Back />\n        </template>\n        \n      </Button>\n      <Button\n        v-else\n        name=\"\"\n        @click=\"$emit('toggleShowTarget', $event)\"\n      >\n        <template #icon>\n          <Effect />\n        </template>\n        \n      </Button>\n      <Button type=\"primary\" name=\"\" @click=\"onDownload\">\n        <template #icon>\n          <Save />\n        </template>\n        \n      </Button>\n    </Space>\n  </Space>\n</template>\n\n<style scoped>\n.result {\n  background-image: linear-gradient(\n      45deg,\n      var(--bg) 25%,\n      transparent 25%,\n      transparent 75%,\n      var(--bg) 75%,\n      var(--bg)\n    ),\n    linear-gradient(\n      45deg,\n      var(--bg) 25%,\n      transparent 25%,\n      transparent 75%,\n      var(--bg) 75%,\n      var(--bg)\n    );\n  background-position:\n    0 0,\n    10px 10px;\n  background-size: 20px 20px;\n}\n</style>\n"],"sourceRoot":""}]);
