`, "",{"version":3,"sources":["webpack://./src/components/cards/DevTool.vue"],"names":[],"mappings":";AAsLA;EACE,gCAAgC;AAClC","sourcesContent":["<script lang=\"ts\">\nimport { defineComponent } from \"vue\";\nimport { Animation, Effect, WebGLEffect } from \"../../types\";\nimport {\n  webglEffectShader,\n  webglLoadEffectShader,\n  webglSetFloat,\n  webglSetVec2,\n} from \"../../utils/webgl\";\nimport Textarea from \"../inputs/Textarea.vue\";\nimport Button from \"../inputs/Button.vue\";\nimport Checkbox from \"../inputs/Checkbox.vue\";\nimport Fieldset from \"../inputs/Fieldset.vue\";\nimport TabGroup from \"../inputs/TabGroup.vue\";\nimport TabButton from \"../inputs/TabButton.vue\";\nimport Space from \"../global/Space.vue\";\n\nconst sampleAnimation = `ctx.drawImage(\n  image, offsetH, offsetV, width, height,\n  cellWidth / 4, cellHeight / 4, cellWidth / 2, cellHeight / 2,\n);`;\n\nconst sampleEffect = \"ctx.translate(0, 0);\";\n\nconst sampleShader = `precision highp float;\nuniform sampler2D texture;\nvarying vec2 vUv;\n\nuniform vec2 resolution;\nuniform float keyframe;\n\nvoid main(void) {\n  gl_FragColor = texture2D(texture, vUv);\n}`;\n\nexport default defineComponent({\n  components: {\n    Textarea,\n    Button,\n    Checkbox,\n    Space,\n    Fieldset,\n    TabGroup,\n    TabButton,\n  },\n  props: {\n    show: { type: Boolean, required: true },\n    noCrop: { type: Boolean, required: true },\n  },\n  emits: [\n    \"update:noCrop\",\n    \"buildAnimation\",\n    \"buildEffect\",\n    \"buildShader\",\n    \"close\",\n  ],\n  data: () => ({\n    tab: \"animation\",\n    source: {\n      animation: sampleAnimation,\n      effect: sampleEffect,\n      webgl: sampleShader,\n    },\n  }),\n  methods: {\n    build(): void {\n      if (this.tab === \"animation\") {\n        this.buildAnimation();\n      } else if (this.tab === \"effect\") {\n        this.buildEffect();\n      } else {\n        this.buildShader();\n      }\n    },\n    /* eslint-disable no-console, no-new-func */\n    buildAnimation(): void {\n      try {\n        const animationImpl = new Function(\n          \"keyframe\",\n          \"ctx\",\n          \"image\",\n          \"offsetH\",\n          \"offsetV\",\n          \"width\",\n          \"height\",\n          \"cellWidth\",\n          \"cellHeight\",\n          this.source.animation,\n        );\n        const animation: Animation = (...args) => {\n          try {\n            animationImpl(...args);\n          } catch (error) {\n            console.log(error);\n          }\n        };\n        this.$emit(\"buildAnimation\", { label: \"\", value: animation });\n      } catch (error) {\n        console.log(error);\n      }\n    },\n    buildEffect(): void {\n      try {\n        const effectImpl = new Function(\n          \"keyframe\",\n          \"ctx\",\n          \"cellWidth\",\n          \"cellHeight\",\n          this.source.effect,\n        );\n        const effect: Effect = (...args) => {\n          try {\n            effectImpl(...args);\n          } catch (error) {\n            console.log(error);\n          }\n        };\n        this.$emit(\"buildEffect\", { label: \"\", value: effect });\n      } catch (error) {\n        console.log(error);\n      }\n    },\n    buildShader(): void {\n      try {\n        const program: WebGLProgram = webglLoadEffectShader(\n          webglEffectShader(this.source.webgl),\n        );\n        const effect: WebGLEffect = (keyframe, w, h) => {\n          webglSetFloat(program, \"keyframe\", keyframe);\n          webglSetVec2(program, \"resolution\", [w, h]);\n          return program;\n        };\n        this.$emit(\"buildShader\", { label: \"\", value: effect });\n      } catch (error) {\n        console.log(error);\n      }\n    },\n    /* eslint-enable */\n  },\n});\n</script>\n\n<template>\n  <Space vertical xlarge full>\n    <TabGroup>\n      <TabButton v-model=\"tab\" value=\"animation\">\n         (js)\n      </TabButton>\n      <TabButton v-model=\"tab\" value=\"effect\">  (js) </TabButton>\n      <TabButton v-model=\"tab\" value=\"webgl\">  (glsl) </TabButton>\n    </TabGroup>\n    <Fieldset label=\"\">\n      <Space vertical full>\n        <p class=\"description\"></p>\n        <p v-if=\"tab === 'animation'\" class=\"description\">\n          args: keyframe, ctx, image, offsetH, offsetV, width, height,\n          cellWidth, cellHeight\n        </p>\n        <p v-if=\"tab === 'effect'\" class=\"description\">\n          args: keyframe, ctx, cellWidth, cellHeight\n        </p>\n        <Textarea v-model=\"source[tab]\" block :rows=\"20\" />\n        <Button @click=\"build\">  </Button>\n      </Space>\n    </Fieldset>\n    <Fieldset label=\"\">\n      <Checkbox\n        :model-value=\"noCrop\"\n        name=\"\"\n        @update:model-value=\"$emit('update:noCrop', $event)\"\n      >\n        {{ \"\" }}\n      </Checkbox>\n    </Fieldset>\n    <Button type=\"text\" @click=\"$emit('close')\">\n      <template #icon>  </template>\n      \n    </Button>\n  </Space>\n</template>\n\n<style scoped>\n.description {\n  font-size: var(--fontSizeMedium);\n}\n</style>\n"],"sourceRoot":""}]);
