.tone-picker[data-v-b2e71ab2] {
  position: relative;
  display: block;
  width: 100%;
  height: 320px; /* override me */
}
.container[data-v-b2e71ab2] {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  overflow: hidden;
  cursor: pointer;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  touch-action: none;
}
.layer[data-v-b2e71ab2] {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.knob[data-v-b2e71ab2] {
  position: absolute;
  /* stylelint-disable-next-line primer/no-undefined-vars */
  top: calc(100% * var(--jsValueV));
  /* stylelint-disable-next-line primer/no-undefined-vars */
  left: calc(100% * var(--jsValueH));
  display: inline-block;
  width: var(--sliderKnobSize);
  height: var(--sliderKnobSize);
  margin-top: calc(-1 * var(--sliderKnobSize) / 2);
  margin-left: calc(-1 * var(--sliderKnobSize) / 2);
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  cursor: grab;
  background-color: transparent;
  border-radius: calc(var(--sliderKnobSize) / 2);
  box-shadow:
    0 0 0 1px inset var(--border),
    0 0 0 2px inset #fff;
}
.knob[data-v-b2e71ab2]:hover {
  box-shadow:
    0 0 0 1px inset var(--primary),
    0 0 0 2px inset #fff;
}
.knob.active[data-v-b2e71ab2] {
  box-shadow:
    var(--primaryShadow),
    0 0 0 1px inset var(--primaryActive),
    0 0 0 2px inset #fff;
}
