ent: [] as ColorStop[],\n        outlines: [] as string[],\n        font: fonts[0].fonts[0].value,\n        /* advanced */\n        lineSpacing: 0.05,\n        padding: 0.02,\n      },\n      showDetails: false,\n      /* internals */\n      running: false,\n      dirty: false,\n    };\n  },\n  computed: {\n    absoluteOutlines(): string[] {\n      return this.conf.outlines.map((outline) =>\n        absColor(outline, this.conf.color),\n      );\n    },\n    absoluteGradient(): { color: string; pos: number }[] {\n      return this.conf.gradient.map((cs) => ({\n        color: absColor(cs.color, this.conf.color),\n        pos: cs.pos,\n      }));\n    },\n  },\n  watch: {\n    conf: {\n      handler(): void {\n        Analytics.changeFont(this.conf.font);\n        this.render(true);\n      },\n      deep: true,\n    },\n    emojiSize: {\n      handler(): void {\n        this.render(true);\n      },\n    },\n  },\n  mounted() {\n    Analytics.changeFont(this.conf.font);\n  },\n  methods: {\n    render(dirty?: boolean): void {\n      if (dirty) {\n        this.dirty = true;\n      }\n      if (!this.dirty || this.running) {\n        return;\n      }\n      this.running = true;\n      this.dirty = false;\n      if (this.conf.content) {\n        const canvas = makeTextImage(\n          this.conf.content,\n          this.conf.color,\n          this.conf.font,\n          this.emojiSize || EMOJI_SIZE,\n          this.conf.align,\n          Number(this.conf.lineSpacing),\n          this.absoluteOutlines,\n          this.absoluteGradient,\n          Number(this.conf.padding),\n        );\n        const name = this.conf.content.replace(/\\n/g, \"\");\n        this.$emit(\"render\", canvas, name);\n      }\n      window.setTimeout(() => {\n        this.running = false;\n        this.render();\n      }, 50);\n    },\n  },\n});\n</script>\n\n<template>\n  <Card v-if=\"show\">\n    <Grid\n      :columns=\"[\n        [450, 1],\n        [Infinity, 3],\n      ]\"\n      spaced\n    >\n